$black: #000;
.nav-link
{
    font-family: 'Steam';
    font-size: 24px;
    font-weight: 400;

    letter-spacing: -.03px;
    text-transform: lowercase;

    opacity: .5;
    color: #303439;
    &:hover
    {
        color: #303439;
    }
}

header
{
    background: url('../img/aboutUsHeader.png') no-repeat center center;
    background-size: cover;
    padding-bottom: 270px;
}
.padding-block
{
    padding: 155px;
    padding-left: 75px;
}

.responsibility-text {
    margin-top: 76px;
}
.perfomance-text {
    margin-top: 50px;
}
.resources-text {
    margin-top: 96px;
}

.contact-text
{
    font-family: 'Steam';
    font-size:55px;
    line-height: 60px;
    font-weight: 700;

    letter-spacing: -.4px;

    color: #303439;
}
.contact-title
{
    font-family: 'Steam';
    font-size: 18px;
    font-weight: 400;

    letter-spacing: -.03px;
    text-transform: uppercase;

    color: #798ea5;
}